
import Vue from 'vue'
import App from './App'
import vueRouter from 'vue-router'
Vue.use(vueRouter)

// 导入路由的包
import router from './router/router'

// 引入mint-ui组件和样式
import MintUI from 'mint-ui'

Vue.prototype.MintUI = MintUI

import 'mint-ui/lib/style.css'
Vue.use(MintUI)

// 引入mui样式
import mui from './lib/mui/css/mui.min.css'
// 引入mui字体图标样式
import './lib/mui/css/icons-extra.css'
import './lib/mui/fonts/mui-icons-extra.ttf'

import $http from 'axios'
Vue.prototype.$http = $http

// 全局配置根路径
$http.defaults.baseURL = "http://www.liulongbin.top:3005/"

//全局过滤器
import moment from 'moment'
Vue.filter('dateFormat', function (datestr, pattern = "YYYY-MM-DD HH:mm:ss") {
  return moment(datestr).format(pattern)
})
// 缩略图插件
import VuePreview from 'vue-preview'
Vue.use(VuePreview)

// 注册 vuex
import Vuex from 'vuex'
Vue.use(Vuex)

var car = JSON.parse(localStorage.getItem('car') || '[]')

var store = new Vuex.Store({
  state: {
    car: car
  },
  mutations: {
    addTocar(state, goodsinfo) {

      var flag = false //假设购物车中没有商品

      state.car.some(item => {
        if (item.id == goodsinfo.id) {
          item.count += parseInt(goodsinfo.count)
          flag = true
          return true
        }
      })
      if (!flag) {
        state.car.push(goodsinfo)
      }

      localStorage.setItem('car', JSON.stringify(state.car))
    },
    updategoodscount(state, goodsinfo) {
      state.car.some(item => {
        if (item.id == goodsinfo.id) {
          item.count = parseInt(goodsinfo.count)
          return true
        }
      })
      localStorage.setItem('car', JSON.stringify(state.car))
    },
    removeshopcar(state, id) {
      state.car.some((item, i) => {
        if (item.id == id) {
          state.car.splice(i, 1)
          return
        }
      })
      localStorage.setItem("car", JSON.stringify(state.car))
    },
    updataedSelected(state, info) {
      state.car.some(item => {
        if (item.id == info.id) {
          item.seleted = info.seleted
        }
      })
      localStorage.setItem("car", JSON.stringify(state.car))
    }

  },
  getters: {
    getAllcount(state) {
      var c = 0
      state.car.forEach(item => {
        c += item.count
      })
      return c
    },
    getgoodscount(state) {
      var o = {}
      state.car.some(item => {
        o[item.id] = item.count
      })
      return o
    },
    // 是否被选中的状态
    getselected(state) {
      var o = {}
      state.car.some(item => {
        o[item.id] = item.seleted
      })
      return o
    },
    // 商品总件数，和价格
    getAllprice(state) {
      var o = {
        count: 0,
        price: 0
      }
      state.car.some(item => {
        if (item.seleted) {
          o.count += item.count
          o.price += item.price * item.count
        }
      })

      return o
    }
  }
})

new Vue({
  el: '#app',
  router,
  render: c => c(App),
  store
})



