<template>
  <div class="goodsdesc-container">
    <h3>{{desc.title}}</h3>
    <hr />
    <div class="content" v-html="desc.content"></div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      desc: {}
    };
  },
  created() {
    this.getdesc();
  },
  methods: {
    getdesc() {
      this.$http
        .get(`api/goods/getdesc/${this.$route.params.id}`)
        .then(result => {
          this.desc = result.data.message[0];
        });
    }
  }
};
</script>
<style scoped >
.content >>> img {
  display: block;
  width: 100%;
  height: 100%;
}
.content >>> p {
  margin-bottom: 0;
}
</style>





