<template>
  <div class="goodsinfo-container">
    <!-- 轮播图区域 -->
    <div class="mui-card">
      <div class="mui-card-content">
        <div class="mui-card-content-inner">
          <swiper :lunboList="lunboList" :isfull="false"></swiper>
        </div>
      </div>
    </div>
    <!-- 商品购买区域 -->
    <div class="father">
      <transition name="ball" @before-enter="beforeEnter" @enter="enter" @after-enter="afterEnter">
        <div class="ball" v-show="ballflag" ref="ball"></div>
      </transition>
    </div>
    <div class="mui-card">
      <div class="mui-card-header">{{goodsinfo.title}}</div>
      <div class="mui-card-content">
        <div class="mui-card-content-inner">
          <p class="price">
            市场价：
            <del>￥{{goodsinfo.market_price}}</del>
            &nbsp;&nbsp;销售价：
            <span class="now_price">{{goodsinfo.sell_price}}</span>
          </p>
          <span>购买数量：</span>
          <numbox :shul="goodsinfo.stock_quantity"></numbox>
          <p class="buy">
            <mt-button type="primary" size="small">立即购买</mt-button>
            <mt-button type="danger" size="small" @click="addcar" :disabled="ballflag">加入购物车</mt-button>
            <!-- 分析： 如何实现加入购物车时候，拿到 选择的数量 -->
            <!-- 1. 经过分析发现： 按钮属于 goodsinfo 页面， 数字 属于 numberbox 组件 -->
            <!-- 2. 由于涉及到了父子组件的嵌套了，所以，无法直接在 goodsinfo 页面zhong 中获取到 选中的商品数量值-->
            <!-- 3. 怎么解决这个问题：涉及到了 子组件向父组件传值了（事件调用机制） -->
            <!-- 4. 事件调用的本质： 父向子传递方法，子调用这个方法， 同时把 数据当作参数 传递给这个方法 -->
          </p>
        </div>
      </div>
    </div>

    <!-- 商品参数区域 -->
    <div class="mui-card">
      <div class="mui-card-header">商品参数</div>
      <div class="mui-card-content">
        <div class="mui-card-content-inner">
          <p>商品货号：{{goodsinfo.goods_no}}</p>
          <p>库存情况：{{goodsinfo.stock_quantity}}件</p>
          <p>上架时间：{{goodsinfo.add_time | dateFormat}}</p>
        </div>
      </div>
      <div class="mui-card-footer">
        <mt-button type="primary" size="large" plain @click="gophotochar(id)">图文介绍</mt-button>
        <mt-button type="danger" size="large" plain @click="gocomment(id)">商品评论</mt-button>
      </div>
    </div>
  </div>
</template>
<script>
import swiper from "../subcomponents/swiper";
import numbox from "../subcomponents/numbox";
export default {
  data() {
    return {
      id: this.$route.params.id,
      lunboList: [],
      goodsinfo: {},
      ballflag: false,
      flag: true,
      selectedCount: 1
    };
  },
  created() {
    this.getlunbo();
    this.getgoodsinfo();
  },
  methods: {
    getlunbo() {
      this.$http.get(`api/getthumimages/${this.id}`).then(result => {
        if (result.data.status == 0) {
          result.data.message.forEach(item => {
            item.img = item.src;
          });
          this.lunboList = result.data.message;
        }
      });
    },
    getgoodsinfo() {
      this.$http.get(`/api/goods/getinfo/` + this.id).then(result => {
        if (result.data.status === 0) {
          this.goodsinfo = result.data.message[0];
        }
      });
    },
    gophotochar(id) {
      this.$router.push({ name: "getdesc", params: { id } });
    },
    gocomment(id) {
      this.$router.push({ name: "goucomment", params: { id } });
    },
    beforeEnter(el) {
      el.style.transform = "translate(0,0)";
    },
    enter(el) {
      el.offsetWidth;
      //获取到小球在页面中的位置
      const ballposition = this.$refs.ball.getBoundingClientRect();
      const badgeposition = document
        .getElementById("badge")
        .getBoundingClientRect();

      let ballX = badgeposition.left - ballposition.left;
      let ballY = badgeposition.top - ballposition.top;

      el.style.transform = `translate(${ballX}px,${ballY}px)`;
      el.style.transition = "all 1s cubic-bezier(.4,-0.3,1,.68)";
    },
    afterEnter(el) {
      this.ballflag = !this.ballflag;
      el.style.display = "none";
      el.style.transform = `translate(0, 0)`;
      this.flag = "true";
    },
    addcar() {
      // if (this.flag) {
      // this.flag = false;
      var shuliang = parseInt(document.getElementById("test").value);

      this.ballflag = !this.ballflag;
      // }
      var goodsinfo = {
        id: this.id,
        count: shuliang,
        price: this.goodsinfo.sell_price,
        seleted: true
      };
      this.$store.commit("addTocar", goodsinfo);
    }
  },
  components: {
    swiper,
    numbox
  }
};
</script>
<style lang="scss" scoped>
.goodsinfo-container {
  background-color: #eee;
  overflow: hidden;
  // position: relative;

  .now_price {
    color: red;
    font-size: 16px;
    font-weight: bold;
  }

  .mui-card-footer {
    display: block;
    button {
      margin: 15px 0;
    }
  }
  .father {
    z-index: 99;
  }

  .ball {
    width: 15px;
    height: 15px;
    border-radius: 50%;
    background-color: red;
    position: absolute;
    z-index: 99999;
    top: 390px;
    left: 152px;
  }
  .buy {
    margin-top: 10px;
  }
}
</style>





