<template>
  <div class="photoinfo-container">
    <h3>{{photoinfo.title}}</h3>
    <p class="subtitle">
      <span>发表时间：{{photoinfo.add_time | dateFormat }}</span>
      <span>点击：{{photoinfo.click}}次</span>
    </p>
    <hr />

    <!-- 缩略图区域 -->
    <vue-preview class="thumbs" :slides="slide1"></vue-preview>
    <!-- 图片内容区域 -->
    <div class="content" v-html="photoinfo.content"></div>
    <!-- 评论子组件 -->
    <cmt-box :id="id"></cmt-box>
  </div>
</template>
<script>
import comment from "../subcomponents/comment";
export default {
  data() {
    return {
      id: this.$route.params.id,
      photoinfo: {},
      list: [],
      slide1: []
    };
  },
  created() {
    this.getPhotoInfo();
    this.getPhoto();
  },
  methods: {
    getPhotoInfo() {
      this.$http.get("api/getimageInfo/" + this.id).then(result => {
        this.photoinfo = result.data.message[0];
      });
    },

    getPhoto() {
      this.$http.get("api/getthumimages/" + this.id).then(result => {
        if (result.data.status === 0) {
          // 循环每个图片数据，补全图片的宽和高
          result.data.message.forEach(item => {
            item.w = 600;
            item.h = 400;
            item.msrc = item.src;
          });
          this.slide1 = result.data.message;
        }
      });
    }
  },
  components: {
    "cmt-box": comment
  }
};
</script>
<style lang="scss" scoped>
.photoinfo-container {
  h3 {
    color: skyblue;
    font-size: 16px;
    text-align: center;
    margin: 15px 0;
  }
  .subtitle {
    display: flex;
    justify-content: space-between;
    margin: 0 10px;
  }
  .content {
    font-size: 13px;
    line-height: 30px;
    padding: 0 5px;
    text-align: left;
  }
  .thumbs {
    /deep/ .my-gallery {
      display: flex;
      overflow: hidden;

      /deep/ figure {
        justify-content: space-between;
        margin: 0;
        width: 33.33%;
        img {
          width: 100%;
        }
      }
    }
  }
}
</style>





