<template>
  <div class="shopcar-container">
    <div class="goodslist">
      <div class="mui-card" v-for="item in shopcarlist" :key="item.id" :goodsid="item.id">
        <div class="mui-card-content">
          <mt-switch
            v-model="$store.getters.getselected[item.id]"
            @change="selectedChanged(item.id,$store.getters.getselected[item.id])"
          ></mt-switch>
          <img :src="item.thumb_path" alt />
          <div class="info">
            <h1>{{ item.title }}</h1>
            <span class="price">￥{{item.sell_price }}</span>
            <number :initcount="$store.getters.getgoodscount[item.id]" :goodsid="item.id"></number>
            <a href="javascript:;" @click="removecar(item.index,item.id)">删除</a>
          </div>
        </div>
      </div>

      <div class="mui-card">
        <div class="mui-card-content">
          <div class="mui-card-content-inner jiesuan">
            <div class="left">
              <p>总计（不含运费）</p>
              <p>
                已勾选商品
                <span class="red">{{$store.getters.getAllprice.count}}</span> 件， 总价
                <span class="red">${{$store.getters.getAllprice.price}}</span>
              </p>
            </div>
            <mt-button type="danger" class="btn">去结算</mt-button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import number from "../subcomponents/numbox";
export default {
  data() {
    return {
      shopcarlist: []
    };
  },
  created() {
    this.getshopcar();
  },
  methods: {
    getshopcar() {
      var iddr = [];
      this.$store.state.car.forEach(item => iddr.push(item.id));
      if (iddr.length <= 0) {
        return;
      }

      this.$http
        .get("api/goods/getshopcarlist/" + iddr.join(","))
        .then(result => {
          if (result.data.status == 0) {
            this.shopcarlist = result.data.message;
          }
        });
    },
    removecar(index, id) {
      this.shopcarlist.splice(index, 1);
      this.$store.commit("removeshopcar", id);
    },
    // 点击改变状态
    selectedChanged(id, val) {
      this.$store.commit("updataedSelected", { id, seleted: val });
    }
  },
  components: {
    number
  }
};
</script>
<style lang="scss" scoped>
.mui-numbox {
  height: 25px;
}
.shopcar-container {
  background-color: #eee;
  overflow: hidden;
  .goodslist {
    .mui-card-content {
      display: flex;
      align-items: center;
      padding: 10px;
    }
    img {
      width: 60px;
      margin-left: 4px;
    }
    h1 {
      font-size: 13px;
      margin-left: 10px;
    }
    .info {
      .price {
        color: red;
        font-weight: bold;
      }
      span {
        margin: 0 6px;
      }
      a {
        margin-left: 5px;
      }
    }
  }
  .jiesuan {
    display: flex;
    justify-content: space-between;
    align-items: center;
    .btn {
      position: absolute;
      top: 24px;
      right: -95px;
    }
    .red {
      color: red;
      font-weight: bold;
      font-size: 16px;
    }
  }
}
</style>





