<template>
  <div class="cmt-container">
    <h3>发表评论</h3>
    <hr />
    <textarea placeholder="请输入要BB的内容（最多BB120字）" maxlength="120" v-model="msg"></textarea>

    <mt-button type="primary" size="large" @click="addcomment">发表评论</mt-button>

    <div class="cmt-list">
      <div class="cmt-item" v-for="(item,i) in comments " :key="item.user_name + i">
        <div
          class="cmt-title"
        >第{{++i}}楼&nbsp;&nbsp;用户：{{item.user_name}}&nbsp;&nbsp;发表时间：{{item.add_time | dateFormat}}</div>
        <div class="cmt-body">{{item.content}}</div>
      </div>
    </div>

    <mt-button type="danger" size="large" plain id="more" @click="addmore">加载更多</mt-button>
  </div>
</template>
<script>
export default {
  data() {
    return {
      pageindex: 1,
      comments: [],
      msg: ""
    };
  },
  props: ["id"],
  methods: {
    // 获取评论
    getComent() {
      this.$http
        .get("api/getcomments/" + this.id + "?pageindex=" + this.pageindex)
        .then(result => {
          this.comments = this.comments.concat(result.data.message);
        });
    },

    // 加载更多
    addmore() {
      this.pageindex++;
      this.getComent();
    },
    //发表评论
    addcomment() {
      if (this.msg.trim().length == 0) {
        this.MintUI.Toast("输入内容不能为空！");
        return;
      }
      this.$http
        .post("api/postcomment/" + this.id, { content: this.msg.trim() })
        .then(result => {
          if (result.data.status === 0) {
            var comment = {
              user_name: "大文杰牛逼",
              add_time: new Date(),
              content: this.msg.trim()
            };
            this.comments.unshift(comment);
            this.msg = "";
          }
        });
    }
  },
  created() {
    this.getComent();
  }
};
</script>
<style lang="scss" scoped>
.cmt-container {
  h3 {
    font-size: 18px;
  }
  textarea {
    font-size: 14px;
    height: 85px;
    margin: 0;
  }

  .cmt-list {
    margin: 5px 0;
    .cmt-item {
      font-size: 13px;
      .cmt-title {
        line-height: 30px;
        background-color: #ccc;
      }
      .cmt-body {
        line-height: 35px;
        text-indent: 2em;
      }
    }
  }
  #more {
    margin-bottom: 5px;
  }
}
</style>





