import Vue from 'vue'
import Router from 'vue-router'
Vue.use(Router)


import home from '../components/tabbar/HomeContainer.vue'
import search from '../components/tabbar/SearchContainer.vue'
import shopcar from '../components/tabbar/ShopContainer.vue'
import vip from '../components/tabbar/VipCotainer.vue'
import newList from '../components/news/newList.vue'
import newsIfon from '../components/news/NewsInfo.vue'
import photolist from '../components/Photo/PhotoList.vue'
import photoinfo from '../components/Photo/PhotoInfo.vue'
import goodslist from '../components/goods/GoodsList.vue'
import goodsinfo from '../components/goods/GoodsInfo.vue'
import goodsphotochar from '../components/goods/goodsphotochar.vue'
import goodscomments from '../components/goods/goodsComments.vue'



export default new Router({
  linkActiveClass: 'mui-active',
  routes: [
    { path: '/', redirect: '/home' },
    { path: '/home', component: home },
    { path: '/vip', component: vip },
    { path: '/shopcar', component: shopcar },
    { path: '/search', component: search },
    { path: '/home/newList', component: newList },
    { path: '/home/newsinfo/:id', component: newsIfon },
    { path: '/home/photolist', component: photolist },
    { path: '/home/photoinfo/:id', component: photoinfo },
    { path: '/home/goodslist', component: goodslist },
    { path: '/home/goodsinfo/:id', component: goodsinfo, name: 'goodsinfo' },
    { path: '/goods/goodsdesc/:id', component: goodsphotochar, name: 'getdesc' },
    { path: '/goods/goodscomment/:id', component: goodscomments, name: 'goucomment' },
  ]
})



